import React, { Component } from 'react'
import CrosswordJSON from '../../assets/MyCrossword'
import './Crossword.css'
import {ListItem, List, Button} from '@material-ui/core'

export default class Crossword extends Component {
    // React Methods

    constructor(props){
        super(props);
        this.myRef = React.createRef();
        this.solution = CrosswordJSON.Grid;
        this.clues = CrosswordJSON.Clues;
        this.gridSize = CrosswordJSON.Size[0]
        this.state = {
            grid: this.initializeUserGridFromSolution(this.solution),
            userPosition: this.initilaizeUserPosition(this.solution),
            userIsAcross: true
        }
        this.clueNumbers = this.initializeClueNumbers(this.state.grid)
    }
    componentDidMount() {
        window.addEventListener("keydown",this.handleKeyDown);
        this.myRef.current.focus();
    }

    componentWillUnmount() {
        window.removeEventListener("keydown",this.handleKeyDown);
    }
    
    // Initialization Methods 
    initializeClueNumbers = (grid) => {
        let counter = 1;
        var clueNumbers = [];
        for(let i = 0; i < grid.length; i++){
            if (grid[i] == null){
                clueNumbers.push(null)
            } else {
                // i - 7 is null/undef then put number
                // i % 7 is 0 then number
                // i - 1 is null/undef then number 
                if (grid[i - 7] == null || i % 7 === 0 || grid[i - 1] == null){
                    clueNumbers.push(counter++);
                } else {
                    clueNumbers.push(null)
                }
            }
        }
        return clueNumbers;
    }

    initializeUserGridFromSolution = (solution) => {
        return solution.map(letter => letter !== "" ? "" : null)
    };

    initilaizeUserPosition = (grid) => {
        return grid.findIndex(element => element !== "")
    };

    // Handle Events
    updateGrid = (key,index) => {
        let newGrid = this.state.grid;
        newGrid[index] = key.toUpperCase();
        this.setState({grid:newGrid})    
    
    }
    handleKeyDown = e => {
        const {key} = e;
        var newPosition;        
        switch(key){
            case "ArrowLeft":
                if (this.state.userPosition % 7 !== 0){
                    newPosition = this.state.userPosition - 1
                    if (this.state.grid[newPosition] != null){
                        this.setState({userPosition: newPosition})
                    }
                }    
                break;
            case "ArrowRight":
                if (this.state.userPosition % 7 !== 6){
                    newPosition = this.state.userPosition + 1
                    if (this.state.grid[newPosition] != null){
                        this.setState({userPosition: newPosition})
                    }
                } 
                break;
            case "ArrowUp":
                newPosition = this.state.userPosition - this.gridSize;
                if (this.state.grid[newPosition] != null){
                    this.setState({userPosition: newPosition})
                }
                break;
            case "ArrowDown":
                newPosition = this.state.userPosition + this.gridSize;
                if (this.state.grid[newPosition] != null){
                    this.setState({userPosition: newPosition})
                }
                break;                
            case "Enter":
                break;
            case "Shift":
                // As of now do nothing
                break;
            case "Backspace":
                this.updateGrid("",this.state.userPosition)
            break;
            case "Meta":
            break;
            default:
                this.updateGrid(key,this.state.userPosition)
            }
    }

    handleCheckWord = e => {
        console.log("Check Word!")
    };

    handleCheckPuzzle = e => {
        console.log("Check Puzzle!")
    };

    handleClear = e => {
        this.setState({grid:this.initializeUserGridFromSolution(this.solution)})
    };

    // Create Elements
    renderSquare = (letter,index) => {
        const contents = <div><sup>{this.clueNumbers[index]}</sup>{letter}</div>
        if(letter === null){
            return <div key={index} className="square square-black"/>;
        }
        if(index === this.state.userPosition){
            return <div key={index} className="square square-user">{contents}</div>
        }
        else {
            return <div key={index} className="square">{contents}</div>
        }
    };

    renderGrid = (grid) => {
        return grid.map((letter,index) => this.renderSquare(letter,index))
    }
    renderCluesList = (clues) => {
        return clues.map((clue,index) => <ListItem key={index}> {clue.number + ". " + clue.clue}</ListItem>)
    };

    render() {
        return (
            <div className="crossword" tabIndex="-1" ref={this.myRef}>
                <div className="actions-menu">
                    <Button variant="contained" onClick={this.handleCheckWord}>Check Word</Button>
                    <Button variant="contained" onClick={this.handleCheckPuzzle}>Check Puzzle</Button>
                    <Button variant="contained" onClick={this.handleClear}>Clear</Button>
                </div>
                <br/> 
                <div className="board-and-clues">
                    <div className="board">
                        {this.renderGrid(this.state.grid)}
                    </div>
                    <div className="clues">
                        <List>
                            <ListItem><b>Across</b></ListItem>
                            {this.renderCluesList(this.clues.Across)}
                        </List>
                    </div>
                    <div className="clues">
                        <List>
                            <ListItem><b>Down</b></ListItem>
                            {this.renderCluesList(this.clues.Down)}
                        </List>
                    </div>
                </div> 
            </div>
        )
    }
}
